        // this.sendTrack(this.state.videoIdx, this.state.type);

        const content = 'https://www.digitimes.com.tw/seminar/IBM_20210104/ajax/testapi/upload/';
        let re = /^[0-9]+$/;
        let emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
        
        function getState(_state) {
            return _state ? "NOT CHECKED" : "CHECKED"
        }

