import React, { Component, Fragment } from 'react'
import YouTube from 'react-youtube';
import { createPortal } from 'react-dom'

import { Consumer } from './context/Context'

import $ from 'jquery'

export default class Video extends Component {
    state = {
        finish: true,
    }
    componentDidMount() {
        $(".video__popup").hide();
    }

    _onReady = (event) => {
        console.log("Ready!")
    }
    _onPlay = (event) => {
        if(!this.state.finish) return;
        this.setState({
            finish: false
        })
        setTimeout(() => {
            event.target.pauseVideo();
            $(".video__popup").fadeIn();
        }, 20000)
        $(document).on('click', '.submit', () => {
            if(!this.props.status) {
                event.target.playVideo(); 
            }
        })
    }

    render() {
        const opts = {
            playerVars: {
              autoplay: 0,
              controls: 1,
              playsinline: 1
            },
        };
        return createPortal(
            
            <Consumer>
                {value => (
                    <Fragment>
                        
                        <div className="video__show">
                            <div className="video__popup">
                                <p>填寫資訊，立即獲得數位轉型關鍵</p>
                                <p>讓H.I.T.為您神助攻！協助您完成數位轉型最後一哩路！</p>
                                <a onClick={() => value.openForm("V")}>獲取完整關鍵報告</a>
                            </div>
                            <YouTube id="videoPlayer" videoId={value.videoItem[value.videoIdx].id} opts={opts} onPlay={this._onPlay} onReady={this._onReady} />
                        </div>
                        <div className="video__nav">
                            <ul>
                                {value.videoItem.map((item,idx) => 
                                    <li key={idx} className={item.state} onClick={() => {value.changeVideo(idx)}}
                                        style={{                            
                                            backgroundImage: `url(https://img.youtube.com/vi/${item.id}/default.jpg)`
                                        }}
                                    >
                                        <div><p>{item.title}</p></div>    
                                    </li>
                                )}
                            </ul>
                        </div>
                    </Fragment>
                )}
            </Consumer>
            ,document.getElementById("youtube")
        )
    }
}
